.publications_section {
  width: 100%;
  float: left;
  position: relative;
  padding-top: 30px;
}

.publications_section::after {
  content: '';
  position: absolute;
  background-color: #f9f9fb;
  height: 100%;
  width: 640px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  top: 0;
  z-index: -1;
}


.publi-carrousel {
  display: flex;
  flex-direction: column;
  background-color: #f9f9fb;
  padding-bottom: 120px;
  align-items: center;
}

.publi h1 {
  color: #11b986;
  font-size: 52px;
  font-weight: bold;
  font-style: normal;
  font-family: sans-serif;
  text-align: center;
}

.ldTBTR {
  right: -7px;
  font-size: 35px;
}

.publi-slide {
  background: #fff;
  display: flex;
  flex-direction: column;
  padding: 40px;
  width: 100%;
  height: 100%;
}

.publi-align-locale {
  display: flex;
  text-align: left;
  margin-bottom: 60px;
}

.publi-align-resume {
  display: flex;
  text-align: justify;
  margin-bottom: 60px;
}

.publi-align-title {
  margin-bottom: 26px;
}

.publi-align-ref {
  margin-bottom: 26px;
}

.internal-button {
  width: 152px;
  height: 37px;
  background: #11b986;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  cursor: pointer;
  color: #fff;
  display: flex;
  align-self: center;
  margin-top: 25px;
}

.profile {
  color: #6B6B6B;
  font-size: 18px;
  font-style: normal;
  font-family: sans-serif;
  text-align: center;
  font-weight: normal;
}

.profile-bold {
  color: #6B6B6B;
  font-size: 22px;
  font-style: normal;
  font-family: sans-serif;
  text-align: center;
  font-weight: bold;
}

.profile-normal {
  color: #6B6B6B;
  font-size: 22px;
  font-style: normal;
  font-family: sans-serif;
  text-align: center;
  font-weight: normal;
}

.ref {
  color: #6B6B6B;
  font-size: 18px;
  font-style: normal;
  font-family: sans-serif;
  font-weight: normal;
}