.footer_section {
  width: 100%;
  float: left;
  background-color: #2a2a2c;
  height: auto;
}

.layout_padding {
  padding-top: 90px;
  padding-bottom: 0px;
}

.footer_logo {
  width: 145px;
  float: left;
  padding-bottom: 5px;
  height: 50px;
}

.dolor_text {
  width: 100%;
  float: left;
  font-size: 18px;
  color: #fffcf4;
  padding-top: 10px;
  margin: 0px;
}

.address_text {
  width: 100%;
  float: left;
  font-size: 24px;
  color: #fffcf4;
  text-transform: uppercase;
  font-weight: bold;
}

.subscribe_bt {
  width: 100%;
  float: left;
}

.subscribe_bt a {
  width: 170px;
  float: left;
  font-size: 18px;
  color: #ffffff;
  background-color: #c9e5fb;
  padding: 5px 0px;
  text-align: center;
  text-transform: uppercase;
  margin-top: 22px;
  border-radius: 20px;
}

.social_icon {
  width: 80%;
  margin: 0 auto;
  border-top: 1px solid #fff;
  margin-top: 90px;
  text-align: center;
  margin-bottom: 40px;
}

.social_icon ul {
  margin: 0px;
  padding: 0px;
  display: inline-block;
}

.social_icon li {
  float: left;
  padding: 20px 3px 0px 3px;
}

.copyright_section {
  width: 100%;
  float: left;
  background-color: #ffffff;
  text-align: center;
  color: #ffffff;
}

.copyright_text {
  width: 100%;
  float: left;
  color: #000;
  text-align: center;
  font-size: 16px;
  margin-left: 0px;
  padding: 10px 0px;
}

.copyright_text a {
  color: #000
}

.copyright_text a:hover {
  color: #679af5;
}