.header_section {
  width: 100%;
  float: left;
  background-image: url(./banner-bg.png);
  height: auto;
  background-size: 100%;
  padding-bottom: 30px;
}

.bg-light {
  background-color: transparent !important;
}

.navbar-expand-lg .navbar-nav .nav-link {
  padding-right: 13px;
  padding-left: 13px;
  font-size: 18px;
  color: #2e2d2e;
  text-transform: uppercase;
}

.navbar-light .navbar-nav .nav-link:focus,
.navbar-light .navbar-nav .nav-link:hover {
  color: #1b5a8d;
}

.logo {
  width: 150px;
  float: left;
  height: 45px;
}

#navbarSupportedContent {
  display: none !important;
}

.navbar-expand-lg .navbar-nav .nav-link {
  display: none;
}

.banner_main {
  width: 100%;
  float: left;
  padding-left: 100px;
}

.banner_taital {
  width: 100%;
  float: left;
  font-size: 40px;
  color: #679af5;
  padding-top: 20px;
  text-transform: uppercase;
  font-weight: bold;
}

.banner_taital_1 {
  width: 100%;
  float: left;
  font-size: 60px;
  color: #323232;
  text-transform: uppercase;
  font-weight: bold;
  line-height: 40px;
}

.banner_text {
  width: 40%;
  float: left;
  font-size: 16px;
  color: #171616;
  margin-left: 0px;
}

.btn_main {
  width: 100%;
  float: left;
  padding-top: 35px;
}

.more_bt {
  width: 170px;
  float: left;
}

.more_bt a {
  width: 100%;
  float: left;
  font-size: 16px;
  color: #ffffff;
  background-color: #05141f;
  text-align: center;
  padding: 10px 0px;
  text-transform: uppercase;
}

.more_bt a:hover {
  color: #000;
  background-color: #ffffff;
}

.costume_header {
  width: 100%;
  float: left;
  background-color: #05141f;
  height: auto;
}

.menu_main {
  width: 100%;
  float: left;
}

.menu_main ul {
  margin: 0px;
  padding: 0px;
}

.menu_main li {
  float: left;
  font-size: 16px;
  color: #ffffff;
  text-transform: uppercase;
  padding: 8px 15px;
}

.menu_main li.active {
  background-color: #c9e5fb;
  color: #000 !important;
}

.menu_main li a {
  color: #ffffff;
  padding: 11px 20px;
}

.menu_main li a:hover {
  color: #ffffff;
  background-color: #c9e5fb;
}

.menu_main_1 {
  width: 30%;
  float: left;
}

.menu_main_1 ul {
  margin: 0px;
  padding: 0px;
}

.menu_main_1 li {
  float: left;
  font-size: 16px;
  color: #ffffff;
  text-transform: uppercase;
  padding: 8px 28px;
}

.menu_main_1 li a {
  color: #ffffff;
}