.team {
  justify-content: space-between;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.team_section {
  width: 100%;
  float: left;
  position: relative;
  padding-top: 30px;
}

.team_section:after {
  content: '';
  position: absolute;
  height: 245px;
  width: 400px;
  top: -30px;
  background-image: url(./img-9.png);
  background-size: 100%;
  background-repeat: no-repeat;
  left: 0;
}

#team h1 {
  color: #11b986;
  font-size: 52px;
  font-weight: bold;
  font-style: normal;
  font-family: sans-serif;
  text-align: center;
  padding-top: 30px;
  padding-bottom: 30px;
}

.team img {
  width: 40%;
  border-radius: 100px;
  margin-bottom: 20px;
}

.team div {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
}

.team .align {
  margin-top: 10px;
  width: 60%;
}

.team .links {
  display: flex;
  flex-direction: row;
}

.team .links img {
  width: 25px;
  margin-top: 10px;
}