.services_section {
  width: 100%;
  float: left;
  position: relative;
  padding-top: 30px;
}

.services_section::after {
  content: '';
  position: absolute;
  background-color: #f9f9fb;
  height: 100%;
  width: 640px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  top: 0;
  z-index: -1;
}

.row {

  padding-bottom: 50px;

}

#our-services h1 {
  color: #11b986;
  font-size: 52px;
  font-weight: bold;
  font-style: normal;
  font-family: sans-serif;
  text-align: center;
  padding-top: 30px;
  padding-bottom: 30px;
}

.services_taital {
  width: 100%;
  float: left;
  font-size: 36px;
  color: #05141f;
  font-weight: 600;
}

.image_2 {
  width: 100%;
  float: left;
  padding-left: 120px;
}

.box_main {
  width: 100%;
  float: left;
  border: 1px solid #d3d3d3;
  height: auto;
  padding: 20px;
  margin-bottom: 20px;
}

.technology_text {
  width: 100%;
  float: left;
  font-size: 24px;
  color: #05141f;
  margin-left: 0px;
  font-weight: 500;
}

.dummy_text {
  width: 100%;
  float: left;
  font-size: 16px;
  color: #545455;
  margin-left: 0px;
}

.readmore_bt {
  width: 170px;
  float: left;
  margin-top: 40px;
  margin-bottom: 60px;
}

.readmore_bt a {
  width: 100%;
  float: left;
  font-size: 16px;
  color: #b6b6b6;
  background-color: #05141f;
  text-align: center;
  padding: 10px 0px;
  border: 1px solid #05141f;
}

.readmore_bt a:hover {
  color: #000;
  background-color: transparent;
  border: 1px solid #000;
}